.menu {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  padding: pxToRem(55px) $spacer-xs 0 $spacer-sm;
  height: 100%;
  width: pxToRem(70px);
  background: var(--color-menu-bg);
  transition: all $d-anim ease-in-out;
  overflow-y: auto;
  z-index: 200;
  @include scrollbar;
  
  * {
    color: var(--color-text);
    @include f-index;
  }
  
  &.is-open {
    width: pxToRem(300px);
  }
  
  &.is-closed {
    padding: pxToRem(55px) $spacer-xs $spacer-sm;
    
    .menu__headline-wrapper,
    .menu__nav {
      display: none;
    }
  }
}

.menu__btn {
  display: grid;
  position: absolute;
  place-items: center;
  align-self: end;
  height: pxToRem(40px);
}

.menu__burger {
  position: relative;
  background: var(--color-menu-burger);
  width: pxToRem(24px);
  height: pxToRem(4px);
  opacity: 1;
  transition: all $d-anim ease-in-out;
  
  &::before,
  &::after {
    content: '';
    position: relative;
    display: block;
    background: var(--color-menu-burger);
    width: pxToRem(24px);
    height: pxToRem(4px);
    transition: all $d-anim ease-in-out;
  }
  
  .no-transition & {
    &::before,
    &::after {
      transition: none !important;
    }
  }
  
  &::before {
    top: pxToRem(8px);
  }
  
  &::after {
    bottom: pxToRem(12px);
  }
  
  .is-open & {
    background: var(--color-menu-bg);;
    
    &::before {
      transform: rotate(45deg);
      top: 0;
    }
    
    &::after {
      transform: rotate(-45deg);
      bottom: pxToRem(4px);
    }
  }
}

.menu__headline-wrapper {
  display: flex;
  align-items: center;
  margin-top: $spacer-xl;
  padding-bottom: $spacer-sm;
  
  .bi-triangle-fill {
    rotate: 180deg;
    margin-right: pxToRem(12px);
    font-size: pxToRem(8px);
    line-height: 1;
    
    &::before {
      vertical-align: pxToRem(-4px);
    }
  }
}

.menu__nav-list,
.menu__sub-nav-list {
  counter-reset: section;
}

.menu__nav-item,
.menu__sub-nav-item {
  &::before {
    counter-increment: section;
    content: counters(section, '.') ' ';
    margin-left: pxToRem(-43px);
  }
  
  &:hover {
    > a {
      text-decoration: underline;
    }
  }
}

.menu__nav-list {
  margin-left: pxToRem(44px);
}

.menu__nav-item {
  margin: pxToRem(10px) 0;
  font-weight: $fw-bold;
}

.menu__sub-nav-list {
  margin-top: pxToRem(10px);
}

.menu__sub-nav-item.is-active {
  &::before,
  .menu__sub-link {
    color: var(--color-highlight-text);
  }
}

.menu__link {
  margin-left: pxToRem(24px);
}

.menu__sub-link {
  margin-left: pxToRem(7px);
  margin-bottom: pxToRem(12px);
}